<template>
  <div ref="bg" class="tab_bg">
    <ChattingList class="list"></ChattingList>
    <div class="shadow_up"></div>
    <ChattingInput class="input"></ChattingInput>
  </div>
</template>

<script>
import ChattingInput from "@/components/ChattingInput.vue";
import ChattingList from "@/components/ChattingList.vue";
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["tab_contents_height"])
  },
  components: { ChattingInput, ChattingList },
  created() {
    this.$nextTick(() => {
      this.$refs.bg.style.height = this.tab_contents_height + "px";
    });
  }
};
</script>

<style scoped>
.tab_bg {
  overflow-y: scroll;

  width: 100%;
}

.list {
  width: 100%;
}

.input {
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
}

.shadow_up {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  box-shadow: 0px 1px 10px lightgray;
}
</style>