<template>
  <div class="root">
    <v-container fluid>
      <h1>CMS</h1>
      <v-row>
        <v-col cols="2">
          <v-subheader>HLS URL :</v-subheader>
        </v-col>
        <v-col cols="8">
          <v-text-field label="URL" v-model="hls_url"></v-text-field>
        </v-col>
        <v-col cols="2">
          <v-btn icon outlined @click="updateHlsUrl">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <v-btn icon outlined class="ml-4" @click="updateMessages">
            <v-icon>mdi-refresh</v-icon>
          </v-btn>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-data-table
            :headers="headers"
            :items="computed_messages"
            :items-per-page="20"
            :sort-by="['time']"
            :sort-desc="[true]"
            class="elevation-1"
          ></v-data-table>
        </v-col>
      </v-row>

      <!-- <v-row>
        <v-col>
          <v-textarea class="mt-5" outlined label="Text"></v-textarea>
        </v-col>
      </v-row> -->
    </v-container>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import * as FB from "../api/firebase";

export default {
  data() {
    return {
      hls_url: "",
      computed_messages: [],
      headers: [
        {
          text: "Time",
          align: "end",
          sortable: true,
          value: "date",
          width: 250
        },
        { text: "Type", value: "type", sortable: false, width: 120 },
        { text: "User", value: "user", sortable: false, width: 120 },
        { text: "Message", value: "message", sortable: false }
      ]
    };
  },
  computed: {
    ...mapGetters(["getHlsUrl"])
  },
  created() {
    this.LOAD_HLS_URL().then(() => {
      this.hls_url = this.getHlsUrl;
    });

    this.updateMessages();
  },
  methods: {
    ...mapActions(["LOAD_HLS_URL"]),
    updateHlsUrl() {
      FB.updateHlsUrl("hls_url", this.hls_url);
    },
    updateMessages() {
      this.computed_messages = [];
      FB.loadAllData().then(snapshot => {
        snapshot.forEach(child => {
          const data = child.val();
          const str_date = new Date(data.time).toLocaleString("ko-KR");

          const obj = {
            ...data,
            date: str_date
          };
          this.computed_messages.push(obj);
        });
      });
    }
  }
};
</script>

<style scoped>

.root {
  background-color: white;
}

</style>