<template>
  <div :style="{ height: v_heith }" ref="area">
    <v-tabs v-model="tab" background-color="primary" dark class="tab_menu">
      <v-tab v-for="item in this.video_items" :key="item.tab">
        {{ item }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab" id="tab_contents">
      <v-tab-item v-for="item in this.video_items" :key="item">
        <div class="tabs" :style="{ height: height }">
          <HLSVideo v-if="item === 'HLS'" />
          <WebRTCReceiver v-else-if="item === 'RECIVER'" />
          <WebRTCSender v-else-if="item === 'SENDER'" />
        </div>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import HLSVideo from "@/components/HLSVideo.vue";
import WebRTCReceiver from "@/components/WebRTCReceiver.vue";
import WebRTCSender from '@/components/WebRTCSender.vue';
import { EventBus, EVENT_SELECT_TOP_TAP } from '../utils/events-bus';

export default {
  data() {
    return {
      tabs: ["HLS", "WEBRTC"],
      height: "0px",
      v_heith: "0px",
      tab: 0
    };
  },
  computed: {
    ...mapState(["video_items"])
  },
  components: {
    HLSVideo,
    WebRTCReceiver,
    WebRTCSender
  },
  props: ["props_tab"],
  created() {
    this.$nextTick(() => {
      const calculate_height = (window.innerWidth * 9) / 16;
      this.v_heith = calculate_height + "px";

      const h = this.$refs.area.getBoundingClientRect().height;
      this.height = h + "px";
      this.SET_TAB_CONTENS_HEIGHT(h);
    });
  },
  watch: {
    tab: function() {
      EventBus.$emit(EVENT_SELECT_TOP_TAP, this.tab);
      // console.log("Tab Chaged", this.tab);
    }
  },
  methods: {
    ...mapMutations(["SET_TAB_CONTENS_HEIGHT"])
  }
};
</script>

<style scoped>
.tab_menu {
  display: none;
}

.tabs {
  z-index: 2;
}

.indicator {
  background-color: transparent;
  width: 100%;
  margin: 0;
  z-index: 1;
}
</style>