<template>
  <div ref="area">
    <v-tabs v-model="tab" background-color="primary" dark class="tab_menu">
      <v-tab v-for="item in this.tab_items" :key="item.tab">
        {{ item }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab" id="tab_contents">
      <v-tab-item v-for="item in this.tab_items" :key="item">
        <div class="tabs" :style="{ height: height }">
          <Chatting v-if="item === 'Chatting'" />
          <Emoji v-else-if="item === 'Emoji'" />
          <Info v-else-if="item === 'Info'" />
          <div />
        </div>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import Info from "@/components/Info.vue";
import Emoji from "@/components/Emoji.vue";
import Chatting from "@/components/Chatting.vue";
import { mapMutations, mapState } from "vuex";
import { EventBus, EVENT_SELECT_BOTTOM_TAP } from "../utils/events-bus";

export default {
  data() {
    return {
      height: "0px",
      tab: 0
    };
  },
  computed: {
    ...mapState(["tab_items"])
  },
  components: {
    Info,
    Chatting,
    Emoji
  },
  created() {
    this.$nextTick(() => {
      const h = this.$refs.area.getBoundingClientRect().height;
      this.height = h + "px";
      this.SET_TAB_CONTENS_HEIGHT(h);
    });
  },
  watch: {
    tab: function() {
      EventBus.$emit(EVENT_SELECT_BOTTOM_TAP, this.tab);
      console.log("Tab Chaged", this.tab);
    }
  },
  methods: {
    ...mapMutations(["SET_TAB_CONTENS_HEIGHT"])
  }
};
</script>

<style scoped>
.tab_menu {
  display: none;
}

.tabs {
  z-index: 2;
}

.indicator {
  background-color: transparent;
  width: 100%;
  margin: 0;
  z-index: 1;
}
</style>