<template>
  <div class="root" :style="{ height: v_height }">
    <video
      autoplay
      playsinline
      muted
      controls
      id="wowza-receiver"
      type="module"
      src="../utils/webrtc/page_play.js"
    ></video>
  </div>
</template>

<script>
import * as Receiver from "../utils/webrtc/page_play";

export default {
  data() {
    return {
      v_height: "0px"
    };
  },
  created() {
    const calculate_height = (window.innerWidth * 9) / 16;
    this.v_height = calculate_height + "px";

    this.$nextTick(() => {
      Receiver.initialize();
    });
  }
};
</script>

<style scoped>
.root {
  position: relative;
  background: black;
}

#wowza-receiver {
  background: black;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
</style>