<template>
  <div ref="bg" class="root">
    <div class="title">User Name</div>
    <form class="horizontal_line">
      <input
        style="flex: 7"
        class="edit"
        type="text"
        v-model="user_name"
        @keyup.enter="applyName"
      />
      <div style="flex: 1" class="divider">:</div>
      <v-btn style="flex: 2" @click="applyName">APPLY</v-btn>
    </form>

    <div class="title">Web RTC</div>

    <div class="horizontal_line">
      <div class="left_option">Signaling URL</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input
        class="edit right_option"
        type="text"
        v-model="web_signaliing_url"
      />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Video Camera</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" />
    </div>

    <div class="title">Receiver</div>

    <div class="horizontal_line">
      <div class="left_option">Stream Name</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="receive_stream" />
    </div>

    <div class="title">Sender</div>

    <div class="horizontal_line">
      <div class="left_option">Stream Name</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="sender_stream" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Video Bitrate</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Video Codect</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" />
    </div>
    <div class="horizontal_line">
      <div class="left_option">Video Bitrate</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Frame Size</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Frame Rate</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" />
    </div>

    <div class="horizontal_line">
      <v-btn style="flex: 1; margin-top: 20px" @click="applyWebRTC"
        >APPLY</v-btn
      >
    </div>

    <div class="horizontal_line">
      <v-btn
        id="publish_btn"
        style="flex: 1; margin-top: 20px"
        @click="publishWebRTC"
        >PUBLISH</v-btn
      >
    </div>

    <div style="width: 100%; height: 40px"></div>
  </div>
</template>

<script>
import * as CONFIG from "../utils/config";
import { mapMutations, mapState } from "vuex";

export default {
  data() {
    return {
      user_name: "",
      web_signaliing_url: "",
      receive_stream: "",
      sender_stream: ""
    };
  },
  computed: {
    ...mapState(["tab_contents_height"])
  },
  created() {
    this.$nextTick(() => {
      this.$refs.bg.style.height = this.tab_contents_height + "px";
      console.log(this.tab_contents_height);
    });

    this.loadInfo();
  },
  methods: {
    ...mapMutations(["SET_NAME"]),
    applyName() {
      CONFIG.put(CONFIG.Key.STR_USER_NAME, this.user_name);
      this.SET_NAME(this.user_name);
    },
    applyWebRTC() {
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_URL, this.web_signaliing_url);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_RECEIVE_STREAM, this.receive_stream);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_SENDER_STREAM, this.sender_stream);
    },
    loadInfo() {
      this.user_name = CONFIG.load(CONFIG.Key.STR_USER_NAME);
      this.web_signaliing_url = CONFIG.load(CONFIG.Key.STR_WEB_RTC_URL);
      this.receive_stream = CONFIG.load(CONFIG.Key.STR_WEB_RTC_RECEIVE_STREAM);
      this.sender_stream = CONFIG.load(CONFIG.Key.STR_WEB_RTC_SENDER_STREAM);
    },
    publishWebRTC() {
      console.log("publish");
    }
  }
};
</script>

<style scoped>
* {
  font-family: "Roboto";
}

.root {
  padding: 0px 20px 0px 20px;
  overflow-y: scroll;
}

.edit {
  width: 100%;
  height: 100%;

  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;

  border-bottom: 1px solid black;
}

.title {
  font-size: 20px;
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 5px;
}

.horizontal_line {
  height: 40px;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.divider {
  text-align: center;
}

.right_input {
  flex: 6;
  margin-left: 20px;
}

.left_option {
  flex: 2;
}

.right_option {
  flex: 2;
  margin-left: 20px;
}
</style>