<template>
  <div class="root" :style="{ height: v_height }">
    <video id="wowza-sender" autoplay playsinline muted controls></video>
  </div>
</template>

<script>
import * as Sender from "../utils/webrtc/publish";

export default {
  data() {
    return {
      v_height: "0px"
    };
  },
  created() {
    const calculate_height = (window.innerWidth * 9) / 16;
    this.v_height = calculate_height + "px";

    this.$nextTick(() => {
      console.log("Sender Created");
      Sender.initialize();
    });
  }
};
</script>

<style scoped>
.root {
  position: relative;
}

#wowza-sender {
  background: black;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
</style>