<template>
  <div class="indicator_bg">
    <div v-for="index in this.props_tab_length" :key="index">
      <span class="dot" :class="getDotClass(index)"></span>
    </div>
  </div>
</template>

<script>
import { EventBus } from "../utils/events-bus";

export default {
  props: ["props_event_type", "props_tab_length"],
  data() {
    return {
      selected_index: 0
    };
  },
  computed: {
  },
  created() {
    EventBus.$on(this.props_event_type, index => {
      this.selected_index = index;
    });
  },
  methods: {
    getDotClass(index) {
      return index - 1 === this.selected_index ? "selected" : "unselected";
    }
  }
};
</script>


<style scoped>
.indicator_bg {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  height: 7px;
}

.dot {
  margin-left: 8px;
  margin-right: 8px;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  display: inline-block;
}

.selected {
  background-color: #bfc2c8;
}

.unselected {
  background-color: #e1e1e1;
}
</style>